<template>
  <div class="content">
    <div class="success-message">
      <div class="success-photo">
        <img class="success-icon" :src="`${$cdnImageUrl}/pc/success.png`"/>
      </div>
      <div class="success-sign">追评成功</div>
      <div class="success-thanks">感谢您为其他小伙伴购物决策提供线索</div>
      <div class="back-home" @click="toHome()">返回首页</div>
      <div class="check" @click="toCheck()">查看评价</div>
    </div>
  </div>
</template>
<script>
import UplusApi from '@uplus/uplus-api';
import semver from '@/utils/semver'
const instance = new UplusApi()
instance.initDeviceReady()
export default {
  data() {
    return {}
  },
  methods: {
    /**
     * @description  返回首页
    */
    toHome() {
      if (this.$util.isHaierApp()) {
        instance.initDeviceReady().then(() => {
          instance.upAppinfoModule.getAppInfo().then(result => {
            let appVersion = result.retData.appVersion
            console.log(appVersion, 'appVersion')
            if (appVersion && semver.gte(appVersion, '6.20.0')) {
              console.log('aaaa')
              this.$util.openNewWindow('https://uplus.haier.com/uplusapp/customize/index.html?close_current_page=true', true, true)
              //
            } else {
              console.log('bbb')
              instance.initDeviceReady().then(function() {
                instance.closeH5ContainerView();
              });
            }
          })
        })
      } else {
        this.$router.push({ path: '/' })
      }
    },
    /**
     * @description  查看评价
    */
    toCheck() {
      this.$router.push({ path: '/viewEvaluation', query: { cOrderSn: this.$route.query.cOrderSn } })
    }
  }
}
</script>
